.container-upload-file{
    // display: table;
    width: 69%;
    margin: auto;
}

.upload-file{
    // width: 24.375em;
    width: 100%;
    height: 11.563em;
    background-color: #FFFFFF;
    border: 1px dashed #6F6F6F;
    padding: 1.25em;
    z-index: 5;
    opacity: 1;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}

.upload-file-over{
    background-color: #258900;
}

.container-label-trascina{
    line-height: 1;
}

.label-trascina{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    font-size: 0.75em;
    color: #6F6F6F;
}

.label-allega{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 0.75em;
    color: #258900;
    cursor: pointer;
}

.container-icon-aggiungi{
    padding-top: 1.05em;
    padding-bottom: 1.25em;
    // padding-left: 9.5em;
    display: inline-block;
}

.icon-aggiungi{
    font-size: 2.875em;
    color: #258900;
    border-radius: 100%;
    box-shadow: 0px 0px 1.25em 3px #323232;
    width: 0.899em;
    height: 0.899em;
    cursor: pointer;
}

.icomoon-Documenti_37:before{
    position: relative;
    top: -0.295em;
    left: -0.06em;
}

.drag-upload-file{
    // width: 24.375em;
    width: 100%;
    height: 11.563em;
    background-color: #FFFFFF;
    border: 1px solid #258900;
    padding: 3.95em;
    background: #258900;
    color: white;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    opacity: 0;
}

.drag-upload-file-second{
    color: white;
    position: absolute;
    padding: 5.95em;
    top: 0;
    left: 0;
    z-index: 0;
    opacity: 0;
}

.dragover-upload-file {
    z-index: 100;
    opacity: 0.9;
}

.second-label{
    font-size: 0.875em;
}

.panel-box {
    margin: 0.312em;
    padding: 0;
    display: inline-block;
    clear: none;
    // width: 24.375em;
    width: 100%;
    height: 11.563em;
    position: relative;
}

.table-file{
    width: 100%;
}

.view-file{
    color: #258900;
    font-size: 1.25em;
    top: 0.187em;
    position: relative;
    float: left;
}

.remove-file{
    color: #258900;
    font-size: 0.938em;
    top: -0.1em;
    position: relative;
    cursor: pointer;
    float: right;
}

.label-file{
    font-size: 0.875em;
    font-family: Arial, Helvetica, sans-serif;
    color: #323232;
}

.container-table{
    height: 7.22em;
    overflow: auto;
}

.container-allega{
    height: 3.125em;
    border-top: 1px solid #CCCCCC;
    padding-top: 0.625em;
    .label-allega{
        font-size: 1em;
    }
    .label-trascina{
        font-size: 1em;
    }
}

.plus-icon{
    font-size: 1.563em;
    color: #258900;
    cursor: pointer;
}